@import '../../styles/mixins';

.home-welcome-page {
  text-align: center;

  .app {
    text-align: center;
  }

  .rekit-logo {
    animation: app-logo-spin infinite 10s linear;
    height: 80px;
  }

  .app-header {
    background-color: #222;
    height: 150px;
    padding: 20px;
    color: white;
    overflow: hidden;
    position: relative;
  }

  .app-title {
    font-size: 1.5em;
  }

  .app-intro {
    font-size: large;
  }

  ul,
  li {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  ul {
    margin-top: 20px;
  }

  li {
    margin-top: 10px;
  }

  a {
    color: #0288d1;
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }
  }

  p.memo {
    width: 500px;
    color: #999;
    font-size: 12px;
    line-height: 150%;
    margin: auto;
  }

  @keyframes app-logo-spin {
    from {
      transform: rotate(0deg);
    }

    to {
      transform: rotate(360deg);
    }
  }
}

.sa-carousel-controls .sa-carousel-control.sa-carousel-next.tablet-control {
  background-image: url(https://az749841.vo.msecnd.net/modulesassets/sfv4/Assets.img.icons.svg.carousel-right-tablet-control.7d881e2….svg);
  background-size: 100% 100%;
  // margin-top: -30px;
  height: 60px;
  width: 32px;
}


.owl-carousel{
  .owl-stage-outer{
    position: relative;
  }


  
  .owl-dots{
    position: absolute;
    bottom: 100px;
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
  }
  .owl-dot{  
    span{
      border-radius: 50%;
      margin: 15px;
      width: 10px;
      height: 10px;
      display: block;
      background: rgb(157, 199, 216);
    }
  }
  
  .active{
    span{
      background: #ffffff;
    }
  }
}

.sa-tabs{
  z-index: 10;
}

.highlights-module.SquaredImagesLayout{
  .owl-dots{
    bottom: 10px;
    right: -25%;
    margin: 5px;
    z-index: 55;
  }

  .owl-dot{  
    span{
      margin: 5px;
      background: rgb(157, 199, 216);
    }
  }
  .active{
    span{
      background: #777676;
    }
  }
}
